/* style.css */
@mixin s-r-1($property) {
	#{$property}: 0;
}

@mixin s-r-2($property) {
	#{$property}: 0.25rem;
}

@mixin s-r-3($property) {
	#{$property}: 0.5rem;
}

@mixin s-r-4($property) {
	#{$property}: 0.75rem;
}

@mixin s-r-5($property) {
	#{$property}: 1rem;
}

@mixin s-r-6($property) {
	#{$property}: 1rem;

	@media (min-width: 80em) {
		#{$property}: 1.25rem;
	}
}

@mixin s-r-7($property) {
	#{$property}: 1rem;

	@media (min-width: 64em) {
		#{$property}: 1.25rem;
	}

	@media (min-width: 80em) {
		#{$property}: 1.5rem;
	}
}

@mixin s-r-8($property) {
	#{$property}: 1rem;

	@media (min-width: 48em) {
		#{$property}: 1.25rem;
	}

	@media (min-width: 64em) {
		#{$property}: 1.5rem;
	}

	@media (min-width: 80em) {
		#{$property}: 2rem;
	}
}

@mixin s-r-9($property) {
	#{$property}: 1.25rem;

	@media (min-width: 48em) {
