	}
}

@mixin br-xxxxl($property) {
	#{$property}: 4.375rem;

	@media (min-width: 48em) {
		#{$property}: 5.625rem;
	}
}

.wp-block-ainoblocks-accordion-faq-block {
	cursor: pointer;
	margin-bottom: 0.5rem;
	background-color: transparent;
	border: none;

	@media (min-width: 48em) {
		margin-bottom: 1rem;
	}

	&.has-border {
		border-bottom: 1px solid #d8d8d8;
	}

	&.has-text-color {
		h3 {
			color: inherit;
		}
	}

	&.is-style-toggle {
		margin-bottom: 0;
	}

	&.is-style-toggle.has-background {
		background-color: transparent !important;

		&:hover {
			transition: all 0.25s;
			background-color: currentColor !important;
		}
	}

	&.is-style-toggle.has-background.open {
		background-color: currentColor !important;
	}

	.accordion {
		display: flex;
		justify-content: space-between;
		position: relative;

		h3 {
			display: flex;
			position: relative;
			width: 100%;
			margin: 0;
			padding-right: 1.5rem;
			align-items: center;

			&:before,
			&:after {
				content: "";
				position: absolute;
				top: 50%;
				right: 0;
				margin: 0 0 -8px;
				background: currentColor;
				border-radius: 2px;
			}
	
			&:before {
				right: 6px;
				width: 2px;
				height: 14px;
				margin-top: -7.5px;
			}
	
			&:after {
				right: 0;
				width: 14px;
				height: 2px;
				margin-top: -1.5px;
			}
		}
	}

	&.is-style-toggle,
	&.is-style-toggle.icon__left {

		.accordion {

			&:before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 32px;
				height: 32px;
				display: block;
				border-radius: 6.25rem;
			}

			h3 {
				height: 32px;
				padding-left: 2.75rem;
		
				&:before {
					left: 15px;
					right: auto;
				}
		
				&:after {
					left: 9px;
					right: auto;
				}
			}
		}
	}

	.panel {
		display: none;
	}

	&.open {

		.panel {
			display: block;
		}

		.accordion {

			h3:before {
				height: 0;
				margin-top: 0;
			}
		}
	}

	&.icon__left {

		.accordion {

			h3 {
				padding-left: 3rem;
		
				&:before {
					left: 6px;
					right: auto;
				}
		
				&:after {
					left: 0;
					right: auto;
				}
			}
		}
	}
}

